.approach {
  box-sizing: border-box;
  // height: 976px;
  display: flex;
  // padding-top: 100px;
  position: relative;
  // display: inline-block;
  // justify-content: space-between;
  background-color: #2c2c2c;
  margin-left: 121px;
  border-radius: 30px 0 0 30px;
  // overflow: hidden;
  // z-index: 1;

  &__info {
    color: white;
    display: flex;
    // justify-content: space-between;
    // align-items: flex-end;

  }

  .approach__text h2 {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    /* or 48px */
    align-items: center;
    color: #FFF;
  }

  &__text {
    width: 362px;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 150%;
    text-align: start;
    margin: 204px;
  }
}

.aproach__image {
  width: 687px;
  height: 680px;
  background-image: url("/images/Photo.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  // align-self: flex-end;
  position: absolute;
  bottom: 0;
  right: 0;

}

.approach__item {
  margin-top: 64px;
}

.approach__icon {
  display: inline-block;
  float: left;
  background-image: url("/images/approach/announcement speaker.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 64px;
  height: 64px;
  background-color: #fff;
  border-radius: 50%;
  margin-top: 10px;
  margin-right: 34px;

  &-table {
    background-image: url("/images/approach/Vector.png");
  }

  &-list {
    background-image: url("/images/approach/Vector-1.png");
  }
}

.approach span {
  width: 240px;
  height: 92px;
}
